@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400&display=swap);
body {
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#controlled-tabpane-new {
  margin: 5rem;
}

.form-control {
  text-align: center;
  justify-content: center;
}

.add-bill-controller {
  margin: 2rem 6rem 0 6rem;
}

input[type='number'] {
  -moz-appearance: textfield;
}

#controlled-tabpane-bills .nav-item.nav-link {
  color: black;
}

#controlled-tabpane-bills .nav-tabs .nav-item.nav-link.active {
  background-color: rgb(90, 90, 90);
  color: white;
  font-size: 1.1rem;
  border-radius: 30px;
}

.table td {
  width: 33%;
}

.app-container {
  width: 500px;
  box-shadow: 2px 5px 20px gray;
  height: 750px;
  margin: 3rem auto;
  text-align: center;
  background-color: #fefefe;
  overflow: auto;
}

.nav-tabs {
  justify-content: center;
  font-size: 2rem;
}

@media only screen and (max-width: 500px) {
  html,
  body {
    width: 100%;
    height: 100%;
  }
  body {
    margin-bottom: 2rem;
  }
  .app-container,
  #root {
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }
  @media only screen and (min-height: 750px) {
    html,
    body {
      width: 100%;
      height: 100%;
    }
    .app-container,
    #root {
      height: 100%;
    }
  }
}

